<template>
	<div id="store-update">
		<public-header title="基本信息设置"></public-header>
		<div class="content">
			<van-cell-group v-if="type == 'title'">
				<van-field
					label="门店名称"
					placeholder="请输入门店名称"
					v-model="store.title"
					input-align="right"
				>
				</van-field>
			</van-cell-group>

			<van-cell-group v-else-if="type == 'telephone'">
				<van-field
					label="联系电话"
					placeholder="请输入门店联系电话"
					v-model="store.telephone"
					input-align="right"
				>
				</van-field>
			</van-cell-group>

			<van-cell-group v-else-if="type == 'address'">
				<van-field
					label="门店地址"
					placeholder="请输入门店地址"
					v-model="store.address"
					input-align="right"
				>
				</van-field>
			</van-cell-group>

			<template v-else-if="type == 'content'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">门店描述</div>
				<van-cell-group>
					<van-field v-model="store.content" type="textarea" rows="3" autosize placeholder="请输入门店描述" />
				</van-cell-group>
			</template>

			<template v-if="type == 'cid'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">所属分类</div>
				<van-checkbox-group v-model="store.category_arr">
					<van-cell-group>
						<van-cell
							v-for="(item,index) in categorys"
							clickable
							:key="item.id"
							:title="item.title"
							@click="onToggleCategory(item.id, index)"
						>
							<van-checkbox :name="item.id" ref="checkboxes" />
						</van-cell>
					</van-cell-group>
				</van-checkbox-group>
			</template>

			<template v-else-if="type == 'notice'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">商户公告</div>
				<van-cell-group>
					<van-field v-model="store.notice" type="textarea" rows="3" autosize placeholder="请输入商户公告" />
				</van-cell-group>
			</template>

			<template v-else-if="type == 'tips'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">商品列表页提示</div>
				<van-cell-group>
					<van-field v-model="store.tips" type="textarea" rows="3" autosize placeholder="用户进入页面后, 将弹框提示设置的内容" />
				</van-cell-group>
			</template>

			<template v-else-if="type == 'business_hours'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">营业时间</div>
				<van-cell-group>
					<van-cell v-for="(item, index) in store.business_hours" :key="index">
						<div slot="title">
							<span @click="onToggleTimePicker(index, 's')">{{item.s}}</span>
							<span class="padding-10-lr">至</span>
							<span @click="onToggleTimePicker(index, 'e')">{{item.e}}</span>
						</div>
						<div slot="right-icon" class="c-primary" @click="onRemoveTime(index)">移除</div>
					</van-cell>
				</van-cell-group>
				<van-cell-group :class="{'margin-10-t': store.business_hours.length > 0}">
					<van-cell >
						<div class="c-primary" slot="title" @click="onAddTime">
							<i class="icon icon-roundadd font-16"></i>添加时间段
						</div>
					</van-cell>
				</van-cell-group>
				<!--时间picker-->
				<van-popup v-model="timePickerShow" position="bottom">
					<van-datetime-picker
						type="time"
						@confirm="onConfirmTime"
						@cancel="onToggleTimePicker"
					/>
				</van-popup>
			</template>

			<!--门店Logo-->
			<template v-else-if="type == 'logo'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">门店Logo</div>
				<uploader max="1" :value="logo" @onGetUrl="onChangeLogo" @delete="onChangeLogo"></uploader>
			</template>

			<!--门店实景-->
			<template v-else-if="type == 'thumbs'">
				<div class="font-14 padding-15-lr padding-10-tb c-gray">门店实景</div>
				<uploader max="7" :value="thumbs" @onGetUrl="onChangeThumbs" @delete="onChangeThumbs"></uploader>
			</template>

			<div class="padding-15">
				<van-button size="normal" type="primary" :disabled="!islegal" block @click="onSubmit">保存修改</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import uploader from "@/components/uploader"
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		data() {
			return {
				timePickerShow: false,

				id: '',
				type: 'title',
				store: {
					title: '',
					logo: '',
					content: '', //描述
					business_hours: [],
					telephone: '',
					address: '',
					thumbs: [],
					cid: '',
					category_arr: [],
					notice: '',
					tips: '',
				},
				categorys: [],
				businessHoursSelected: {
					index: 0,
					type: 's'
				},
				logo: [],
				thumbs: [],
				islegal: false,
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
			uploader
		},
		methods: {
			onToggleTimePicker(index, type) {
				this.timePickerShow = !this.timePickerShow;
				if(index > -1 && (type == 's' || type == 'e')) {
					this.businessHoursSelected.index = index;
					this.businessHoursSelected.type = type;
				}
			},
			onConfirmTime(value) {
				if(value) {
					let index = this.businessHoursSelected.index;
					let type = this.businessHoursSelected.type;
					this.store.business_hours[index][type] = value;
					this.onToggleTimePicker();
				}
			},
			onAddTime() {
				if(this.type != 'business_hours') {
					return false;
				}
				if(this.store.business_hours.length >=3 ) {
					this.util.$toast('最多可添加三个营业时间段')
					return false;
				}
				this.store.business_hours.push({
					s: '08:00',
					e: '21:00'
				});
			},
			onRemoveTime(index) {
				if(this.type != 'business_hours') {
					return false;
				}
				this.store.business_hours.splice(index, 1);
			},
			onToggleCategory(id, index) {
				this.$refs.checkboxes[index].toggle();
			},
			onChangeLogo(data) {
				let logo = ''
				if(data && data.length == 1 && data[0].filename) {
					logo = data[0].filename;
				}
				this.store.logo = logo;
			},
			onChangeThumbs(data) {
				let thumbs = [];
				if(data && data.length > 0) {
					for(var x in data) {
						thumbs.push({
							image: data[x].url,
							url: ''
						})
					}
				}
				this.store.thumbs = thumbs;
			},
			onLoad() {
				var that = this;
				getData({
					url: 'plateform/store/shop/setting',
					vue: that,
					autoAssign: true,
					variable: 'store',
					data: {
						id: that.id,
						type: that.type
					},
					success: function(res) {
						that.islegal = true;
						if(that.type == 'cid') {
							let categorys = res.categorys;
							if(categorys && categorys.length > 0) {
								that.categorys = [...that.categorys, ...categorys];

							} else {
								that.util.$toast('平台暂未设置商户分类', that.util.getUrl({path: 'pages/merchant/store/basic', query: {id: that.id}}), 2000, 'replace');
								return false;
							}
						} else if(that.type == 'logo') {
							if(that.store.logo) {
								that.logo.push({
									url: that.store.logo
								})
							}
						} else if(that.type == 'thumbs') {
							if(that.store.thumbs && that.store.thumbs.length > 0) {
								for(var x in that.store.thumbs) {
									that.thumbs.push({
										url: that.store.thumbs[x].image
									})
								}
							}
						}
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				let value = that.store[that.type];
				if(that.type == 'cid') {
					value = that.store['category_arr'];
				}
				let redirect = 'pages/merchant/store/basic';
				if(that.type == 'notice' || that.type == 'tips') {
					redirect = 'pages/merchant/store/index';
				}
				setData({
					url: 'plateform/store/shop/setting',
					vue: that,
					data: {
						id: that.id,
						type: that.type,
						value: JSON.stringify(value),
					},
					redirect: that.util.getUrl({path: redirect, query: {id: that.id}}),
					fail: function(res) {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			}
		},
		created() {
			if(this.$route.query) {
				if(this.$route.query.type) {
					this.type = this.$route.query.type;
				}
				if(this.$route.query.id) {
					this.id = this.$route.query.id;
				}
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style scoped>

</style>


